
// Module w3c/l10n
// Looks at the lang attribute on the root element and uses it to manage the config.l10n object so
// that other parts of the system can localise their text

define(
    'geonovum/l10n',[],
    function () {
        var l10n = {
            en: {
                    this_version:               "This version:"
                ,   latest_published_version:   "Latest published version:"
                ,   latest_editors_draft:       "Latest editor's draft:"
                ,   editor:                     "Editor:"
                ,   editors:                    "Editors:"
                ,   author:                     "Author:"
                ,   authors:                    "Authors:"
                ,   abstract:                   "Abstract"
                ,   sotd:                       "Status of This Document"
                ,   status_at_publication:      "This section describes the status of this document at the time of its publication. Other documents may supersede this document. A list of current W3C publications and the latest revision of this technical report can be found in the <a href='https://www.w3.org/TR/'>W3C technical reports index</a> at https://www.w3.org/TR/."
                ,   toc:                        "Table of Contents"
                ,   note:                       "Note"
                ,   fig:                        "Fig. "
                ,   bug_tracker:                "Bug tracker:"
                ,   file_a_bug:                 "file a bug"
                ,   open_bugs:                  "open bugs"
                ,   open_parens:                "("
                ,   close_parens:               ")"
            }
            ,   ko: {
                    this_version:               " :"
                ,   latest_published_version:   " :"
                ,   latest_editors_draft:       "Latest editor's draft:"
                ,   editor:                     "Editor:"
                ,   editors:                    "Editors:"
                ,   author:                     ":"
                ,   authors:                    ":"
                ,   abstract:                   ""
                ,   sotd:                       "  "
                ,   status_at_publication:      "This section describes the status of this document at the time of its publication. Other documents may supersede this document. A list of current W3C publications and the latest revision of this technical report can be found in the <a href='https://www.w3.org/TR/'>W3C technical reports index</a> at https://www.w3.org/TR/."
                ,   toc:                        "Table of Contents"
                ,   note:                       "Note"
                ,   fig:                        " "
                ,   bug_tracker:                "Bug tracker:"
                ,   file_a_bug:                 "file a bug"
                ,   open_bugs:                  "open bugs"
                ,   open_parens:                "("
                ,   close_parens:               ")"
            }
            ,   zh: {
                    this_version:               ""
                ,   latest_published_version:   ""
                ,   latest_editors_draft:       ""
                ,   editor:                     ""
                ,   editors:                    ""
                ,   author:                     "Author:"
                ,   authors:                    "Authors:"
                ,   abstract:                   ""
                ,   sotd:                       ""
                ,   status_at_publication:      "W3C <a href='https://www.w3.org/TR/'>W3C</a>"
                ,   toc:                        ""
                ,   note:                       ""
                ,   fig:                        ""
                ,   bug_tracker:                ""
                ,   file_a_bug:                 ""
                ,   open_bugs:                  ""
                ,   open_parens:                ""
                ,   close_parens:               ""
            }
            ,   ja: {
                    this_version:               ""
                ,   latest_published_version:   ""
                ,   latest_editors_draft:       ""
                ,   editor:                     ""
                ,   editors:                    ""
                ,   author:                     ""
                ,   authors:                    ""
                ,   abstract:                   ""
                ,   sotd:                       ""
                ,   status_at_publication:      "W3C <a href='https://www.w3.org/TR/'>W3C technical reports index</a> (https://www.w3.org/TR/)"
                ,   toc:                        ""
                ,   note:                       ""
                ,   fig:                        ""
                ,   bug_tracker:                ""
                ,   file_a_bug:                 "File a bug"
                ,   open_bugs:                  ""
                ,   open_parens:                ""
                ,   close_parens:               ""
            }
            ,   nl: {
                    this_version:               "Deze versie:"
                ,   latest_published_version:   "Laatst gepubliceerde versie:"
                ,   latest_editors_draft:       "Laatste werkversie:"
                ,   editor:                     "Auteur:"
                ,   editors:                    "Auteurs:"
                ,   author:                     "Author:"
                ,   authors:                    "Authors:"
                ,   abstract:                   "Samenvatting"
                ,   sotd:                       "Status van dit document"
                ,   status_at_publication:      "Deze paragraaf beschrijft de status van dit document ten tijde van publicatie. Andere documenten kunnen dit document vervangen. Een lijst van actuele Geonovum publicaties en meest recente revisie van dit document kan worden gevonden op <a href='http://www.geonovum.nl'>Geonovum website </a> op http://www.geonovum.nl."
                ,   toc:                        "Inhoudsopgave"
                ,   note:                       "Noot"
                ,   fig:                        "Dien een melding in"
                ,   bug_tracker:                "Meldingensysteem:"
                ,   file_a_bug:                 "Dien een melding in"
                ,   open_bugs:                  "open meldingen"
                ,   open_parens:                "("
                ,   close_parens:               ")"
            }
        };
        l10n["zh-hans"] = l10n.zh;
        l10n["zh-cn"] = l10n.zh;

        return {
            run:    function (config, doc, cb) {
                var lang = $(doc.documentElement).attr("lang") || "en";
                config.l10n = l10n[lang] ? l10n[lang] : l10n.en;
                cb();
            }
        };
    }
);

